/* eslint-disable max-lines */
import React, { useEffect, useMemo, useState } from 'react';
import { Row, Fade, Button } from 'reactstrap';
import { useDispatch } from 'react-redux';
import { match, Link } from 'react-router-dom';
import { get, size } from 'lodash';
import moment from 'moment';
import DataTable from '../../components/DataTable';
import {  getDetailReport } from '../../redux/BaoCaoLamViec/action';
import ButtonGoBack from '../../components/Button/ButtonGoBack';
// eslint-disable-next-line import/imports-first

interface Props {
  match: match;
}

// eslint-disable-next-line max-lines-per-function
const BaoCaoLamViec: React.FC<Props> = (props: Props): JSX.Element => {
  const dispatch = useDispatch();
  const [data, setData] = useState<any>([]);
  const idBaoCao = get(props, 'match.params.idBaoCao');
  useEffect(() => {  
    dispatch(
        getDetailReport({idBaoCao: idBaoCao},
        (data: any) => {
          setData(data)
        },
        () => {}
      )
    )
  }, []);
  const columns = useMemo(
    // eslint-disable-next-line max-lines-per-function
    () => [
      {
        Header: 'ID',
        accessor: 'id',
      },
      {
        Header: 'Nội dung',
        accessor: 'content',
      },
      {
        Header: 'Thời gian',
        accessor: 'date',
        Cell: ({ row }: any): JSX.Element => (
            <>{moment(get(row,'values.date','')).format('hh:mm DD/MM/YYYY')}</>
        ),
      },
      {
        Header: 'Bệnh viện',
        accessor: 'hospital',
      },
      {
        Header: 'Bác sĩ',
        accessor: 'doctor',
      },
      {
        Header: 'Vị trí',
        accessor: 'location',
        Cell: ({ row }: any): JSX.Element => (
            <div>{get(row,'values,location','')}</div>
        ),
      },
      {
        Header: 'Ghi chú',
        accessor: 'note',
      },
      {
        Header: 'Quản trị',
        accessor: '',
        Cell: ({ row }: any): JSX.Element => (
          <Link
            className="SipTableFunctionIcon"
            to={`/in-don-hang/${idBaoCao}?idChuyenThu=${get(row, 'values.PACKAGE_ID', '')}`}
            target="_blank"
          >
            <img title="In" src={'../../assets/img/icon/iconPrint.svg'} alt="VTPostek" />
          </Link>
        ),
      },
    ],
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [],
  );

  function renderTable(): JSX.Element {
    return (
      <Row className="sipTableContainer sipTableRowClickable">
        <DataTable columns={columns} data={data} onRowClick={() => {}} />
      </Row>
    );
  }
  return size(data) > 0 ? (
    <>
      <Row className="mb-3 sipTitleContainer">
        <h1 className="sipTitle">
            <ButtonGoBack />
            {'Thông tin chi tiết'}
        </h1>
        {/* <RootTypeahead
          id="location"
          //  labelKey={labelKeyLocation}
          options={listLocation}
          placeholder="Chọn đơn vị"
        // onInputChange={setKeywords}
        //  onChange={handleSelectedLocation}
        //  selected={selectedLocation}
        >
          <span
            style={{
              position: 'absolute',
              right: '8px',
              top: '10px',
            }}
            className="fa fa-caret-down"
          />
        </RootTypeahead> */}
        <div className="sipTitleRightBlock">
          <Button className="ml-2" color="primary" >
          <i className="fa fa-pencil mr-2" />
            {'Thêm mới'}
          </Button>
          {/* <Button className="ml-2" color="primary" >
            <i className="fa fa-pencil mr-2" />
            {'Sửa phiếu gửi'}
          </Button> */}
          {/* <Button className="ml-2" color="primary">
            <img src={'../../assets/img/icon/iconInBuuGui.svg'} alt="VTPostek" />
            {'In bưu gửi'}
          </Button> */}
        </div>
      </Row>
      <h1 className="sipTitle">{'Danh sách phiếu gửi'}</h1>
      {renderTable()}
    </>
  ) : (
    <Fade in={true} timeout={1000}>
      <Row className="mb-3 sipTitleContainer">
        <h1 className="sipTitle">
          <ButtonGoBack />
          {'Quay lại'}
        </h1>
      </Row>
      <div className="row mb-5" />
      <h3 className="text-center">{'Không tìm thấy thông tin phiếu gửi!'}</h3>
    </Fade>
  );
};

export default BaoCaoLamViec;
